import React, { useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserContext } from '../contexts/UserContext'; // Import UserContext
import './MyPage.css';

const MyPage = () => {
  const { user, setUser } = useContext(UserContext); // Use user and setUser from context
  const navigate = useNavigate();

  const handleLogout = () => {
    localStorage.removeItem('token');
    localStorage.removeItem('userId');
    setUser(null);
    navigate('/login');
  };

  return (
    <div className="mypage-container">
      <h2 className="mypage-header"></h2>
      {user ? (
        <div className="auth-buttons">
          <p> {user.nickname}. </p>
          <button className="logout-button" onClick={handleLogout}></button>
        </div>
      ) : (
        <div className="auth-buttons">
          <button onClick={() => navigate('/login')}></button>
          <button onClick={() => navigate('/register')}></button>
        </div>
      )}
      <div className="extra-options">
        <button onClick={() => navigate('/budget-input')}></button>
        <button onClick={() => navigate('/fixed-expenses')}></button>
      </div>
      <div className="links-section">
        <button onClick={() => navigate('/notices')}></button>
        <button onClick={() => navigate('/help')}></button>
      </div>
    </div>
  );
};

export default MyPage;